<template>
    <div>
        <span class="pr_t">{{ txt1 }}</span>
        <span>{{ txt2 }}</span>
    </div>
</template>

<script>
export default {
    props: { rmd: { type: Object } },
    computed: {
        txt1() {
            let res = this.rmd.send_date_real_str
            res = res ? res.split('-') : [ ]
            return res && res.length > 0 ? (res[0] + '' + res[1] + '') : '' // moment(res).format('MMDD')
        },
        txt2() {
            let res = this.rmd.send_date_since_real_str
            res = res ? res.split('-') : [ ]
            return res && res.length > 0 ? (res[0] + '' + res[1] + '') : '' // moment(res).format('MMDD')
        }
    }
}
</script>

<style>

</style>


